.resume-button {
    background-color: transparent;
    border: 3px solid black;
    border-radius: 10px;
    padding: 10px;
    margin-top: 20px;
    font-size: 20px;
    cursor: pointer;
    transition: 0.3s;
}

.resume-button:hover {
    transform: scale(1.1);
    transition: 0.3s;
}